import React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import './style.css';
// import { Container } from './styles';

class Modal {
  showModal = async (titulo, menssagem, funcConfirme) => {
    const botoes = [];

    if (funcConfirme) {
      botoes.push(
        {
          label: 'Sim',
          onClick: () => funcConfirme(),
        },
        {
          label: 'No',
        }
      );
    } else {
      botoes.push({
        label: 'Entendi',
      });
    }

    confirmAlert({
      title: titulo,
      message: menssagem,
      buttons: botoes,
      closeOnEscape: true,
      closeOnClickOutside: true,
    });
  };

  fileName = async (docId, file, anexar) => {
    const data = new FormData();
    data.append('file', file.target.files[0]);
    let teste = '';
    confirmAlert({
      customUI: ({ onClose }) => {
        return (
          <div className="custom-ui">
            <h3>Qual o nome para este arquivo?</h3>
            <input
              className="form-control"
              placeholder="Nome do arquivo"
              onChange={e => {
                teste = e.target.value;
              }}
            />
            <div className="infoErro">
              <span id="labelError" className="oculto">
                Informe o nome do arquivo !
              </span>
            </div>
            <div className="botoes">
              <button
                type="button"
                className="btn principal"
                onClick={() => {
                  if (teste !== '') {
                    data.append('nome', teste);
                    anexar(docId, data);
                    onClose();
                  } else {
                    document
                      .getElementById('labelError')
                      .classList.remove('oculto');
                  }
                }}
              >
                anexar
              </button>
              <button
                type="button"
                className="btn sec"
                onClick={() => {
                  onClose();
                }}
              >
                cancelar
              </button>
            </div>
          </div>
        );
      },
    });
  };
}

export default new Modal();
