import { toast } from 'react-toastify';
import api from './api';

class ServiceFuncionario {
  consultaFuncionario = async id => {
    try {
      const func = await api
        .get(`funcionario/busca/${id}`)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return func;
    } catch (err) {
      return null;
    }
  };

  listaFuncionario = async (busca, pagina = 1) => {
    try {
      const filtro = busca && busca !== '' ? `&busca=${busca}` : '';

      const totalRegistros = 2;

      const func = await api
        .get(
          `funcionario/busca-filtro?pagina=${pagina}&tamanho=${totalRegistros}${filtro}`
        )
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return func;
    } catch (err) {
      return null;
    }
  };

  excluirFuncionario = async funcId => {
    try {
      const func = await api
        .delete(`funcionario/deletar/${funcId}`)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return func;
    } catch (err) {
      return null;
    }
  };

  salvarEditarFuncionario = async (funcionario, edit) => {
    try {
      const data = {
        nome: funcionario.nome,
        cpf: funcionario.cpf,
        rg: funcionario.rg,
        sexo_id: funcionario.sexo_id,
        cargo_id: funcionario.cargo_id,
      };
      if (funcionario.banco_id !== '') {
        data.banco_id = funcionario.banco_id;
      }
      if (funcionario.categoria_cnh_id !== '') {
        data.categoria_cnh_id = funcionario.categoria_cnh_id;
      }
      if (funcionario.conta !== '') {
        data.conta = funcionario.conta;
      }
      if (funcionario.cpf_favorecido !== '') {
        data.cpf_favorecido = funcionario.cpf_favorecido;
      }
      if (funcionario.data_admissao !== '') {
        data.data_admissao = funcionario.data_admissao;
      }
      if (funcionario.data_demissao !== '') {
        data.data_demissao = funcionario.data_demissao;
      }
      if (funcionario.data_nascimento !== '') {
        data.data_nascimento = funcionario.data_nascimento;
      }
      if (funcionario.digito_agencia !== '') {
        data.digito_agencia = funcionario.digito_agencia;
      }
      if (funcionario.digito_conta !== '') {
        data.digito_conta = funcionario.digito_conta;
      }
      if (funcionario.escolaridade_id !== '') {
        data.escolaridade_id = funcionario.escolaridade_id;
      }

      if (funcionario.estado_civil_id !== '') {
        data.estado_civil_id = funcionario.estado_civil_id;
      }
      if (funcionario.nome_favorecido !== '') {
        data.nome_favorecido = funcionario.nome_favorecido;
      }
      if (funcionario.num_carteira_trabalho !== '') {
        data.num_carteira_trabalho = funcionario.num_carteira_trabalho;
      }
      if (funcionario.numero_agencia !== '') {
        data.numero_agencia = funcionario.numero_agencia;
      }
      if (funcionario.rg !== '') {
        data.rg = funcionario.rg;
      }

      if (edit) {
        const func = await api
          .put(`funcionario/editar/${funcionario.id}`, data)
          .then(retorno => {
            return retorno;
          })
          .catch(err => {
            return err.response;
          });
        return func;
      }
      const func = await api
        .post(`funcionario/novo`, data)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });
      return func;
    } catch (err) {
      console.log(err);
      return null;
    }
  };

  salvarTelefone = async (funcId, tipoId, numero) => {
    try {
      const data = {
        funcionario_id: funcId,
        tipo_telefone_id: tipoId,
        numero,
      };

      const func = await api
        .post(`telefone/novo`, data)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return func;
    } catch (err) {
      return null;
    }
  };

  editarTelefone = async (id, tipoId, numero) => {
    try {
      const data = {
        tipo_telefone_id: tipoId,
        numero,
      };

      const func = await api
        .put(`telefone/editar/${id}`, data)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return func;
    } catch (err) {
      return null;
    }
  };

  excluirTelefone = async idTelefone => {
    try {
      const func = await api
        .delete(`telefone/deletar/${idTelefone}`)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          toast.error(
            err.response.data.data
              ? err.response.data.data.motivo
              : 'Erro ao excluir telefone'
          );
          return null;
        });

      return func;
    } catch (err) {
      toast.error('Falha ao excluir telefone');
      return null;
    }
  };

  salvarEndereco = async (id, endereco) => {
    try {
      const data = {
        funcionario_id: id,
        bairro: endereco.bairro,
        cep: endereco.cep,
        cidade: endereco.cidade,
        complemento: endereco.complemento,
        endereco: endereco.endereco,
        estado: endereco.estado,
        numero: endereco.numero,
      };

      const func = await api
        .post(`endereco/novo`, data)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return func;
    } catch (err) {
      return null;
    }
  };

  editarEndereco = async (id, idFuncionario, endereco) => {
    try {
      const data = {
        funcionario_id: idFuncionario,
        bairro: endereco.bairro,
        cep: endereco.cep,
        cidade: endereco.cidade,
        endereco: endereco.endereco,
        estado: endereco.estado,
      };

      if (endereco.numero !== '') {
        data.numero = endereco.numero;
      }

      if (endereco.complemento !== '') {
        data.complemento = endereco.complemento;
      }

      const func = await api
        .put(`endereco/editar/${id}`, data)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return func;
    } catch (err) {
      return null;
    }
  };

  excluirEndereco = async idEndereco => {
    try {
      const func = await api
        .delete(`funcionario/deletar/${idEndereco}`)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          toast.error(
            err.response.data.data
              ? err.response.data.data.motivo
              : 'Erro ao excluir endereo'
          );
          return null;
        });

      return func;
    } catch (err) {
      toast.error('Falha ao excluir endereo');
      return null;
    }
  };

  salvarEmail = async (funcId, tipoId, email) => {
    try {
      const data = {
        funcionario_id: funcId,
        tipo_email_id: tipoId,
        email,
      };
      console.log(data);

      const func = await api
        .post(`email/novo`, data)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return func;
    } catch (err) {
      return null;
    }
  };

  editarEmail = async (id, tipoId, email) => {
    try {
      const data = {
        tipo_email_id: tipoId,
        email,
      };

      const func = await api
        .put(`email/editar/${id}`, data)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return func;
    } catch (err) {
      return null;
    }
  };

  excluirEmail = async idEmail => {
    try {
      const func = await api
        .delete(`email/deletar/${idEmail}`)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          toast.error(
            err.response.data.data
              ? err.response.data.data.motivo
              : 'Erro ao excluir email'
          );
          return null;
        });

      return func;
    } catch (err) {
      toast.error('Falha ao excluir email');
      return null;
    }
  };

  excluirDocumento = async idDocumento => {
    try {
      const func = await api
        .delete(`documento/deletar/${idDocumento}`)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          toast.error(
            err.response.data.data
              ? err.response.data.data.motivo
              : 'Erro ao excluir documento'
          );
          return null;
        });

      return func;
    } catch (err) {
      toast.error('Falha ao excluir documento');
      return null;
    }
  };

  salvarDocumento = async documento => {
    try {
      const data = {
        funcionario_id: documento.funcionario_id,
        nome: documento.nome,
        descricao: documento.descricao,
        tipo_documento_id: documento.tipo_documento_id,
        arquivos: documento.arquivos,
      };

      const doc = await api
        .post(`documento/novo`, data)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return doc;
    } catch (err) {
      return null;
    }
  };

  editarDocumento = async documento => {
    try {
      const data = {
        nome: documento.nome,
        descricao: documento.descricao,
        tipo_documento_id: documento.tipo_documento_id,
        funcionario_id: documento.funcionario_id,
        arquivos: documento.arquivos,
      };

      const doc = await api
        .put(`documento/editar/${documento.id}`, data)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return doc;
    } catch (err) {
      return null;
    }
  };
}

export default new ServiceFuncionario();
