import React from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Spinner from 'react-bootstrap/Spinner';
import { useDispatch, useSelector } from 'react-redux';
import { loginRequest } from '../../store/modules/auth/actions';
import { Content } from './styles';

export default function Login() {
  const [email, setEmail] = React.useState('master@erp.com');
  const [senha, setSenha] = React.useState('123456');

  const dispatch = useDispatch();
  const loading = useSelector(state => state.auth.loading);

  function handleSubmit() {
    dispatch(loginRequest(email, senha));
  }

  return (
    <Content>
      <div className="faixa" />
      <div className="login">
        <div className="top-login">
          <h3>Usurio de acesso</h3>
        </div>
        <Form>
          <Form.Group>
            <Form.Label>Usurio</Form.Label>
            <Form.Control
              type="e-mail"
              value={email}
              onChange={e => setEmail(e.target.value)}
            />
          </Form.Group>

          <Form.Group>
            <Form.Label>Senha</Form.Label>
            <Form.Control
              type="password"
              value={senha}
              onChange={e => setSenha(e.target.value)}
            />
          </Form.Group>
          <Button className="loginSec">Esqueceu sua senha?</Button>
          <Button onClick={() => handleSubmit()} className="loginPrincipal">
            {loading ? <Spinner animation="border" size="sm" /> : 'Confirmar'}
          </Button>
        </Form>
      </div>
    </Content>
  );
}
