import api from './api';

class ServiceUtil {
  selects = async () => {
    try {
      const selects = await api
        .get('usuario/selects')
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return selects;
    } catch (err) {
      return null;
    }
  };

  salvarArquivo = async file => {
    try {
      const arquivo = await api
        .post('arquivo/store', file)
        .then(retorno => {
          return retorno;
        })
        .catch(err => {
          return err.response;
        });

      return arquivo;
    } catch (err) {
      return null;
    }
  };
}

export default new ServiceUtil();
