import React from 'react';
import PropTypes from 'prop-types';
import { Container, Row, Col, Form, Button, Card } from 'react-bootstrap';
import { toast } from 'react-toastify';
import * as Yup from 'yup';
import ServiceFuncionario from '../../services/serviceFuncionario';
import { Content } from './style';
import OptiosEstado from '../optionsEstados';
import Modal from '../modal/index';
import InputText from '../form/inputText';
import InputOptions from '../form/inputOptions';

export default function ListaEnderecos(props) {
  const { data, funcionario } = props;
  const [listEnderecos, setListEnderecos] = React.useState(data);
  const [errosForm, setErrosForm] = React.useState();

  function changeInput(key, campo, e) {
    setListEnderecos(
      listEnderecos.map(item =>
        item.id === key ? { ...item, [campo]: e.target.value } : item
      )
    );
  }

  const handleEditar = id => {
    setListEnderecos(
      listEnderecos.map(item =>
        item.id === id ? { ...item, editando: true } : item
      )
    );
  };

  const handleCancelar = id => {
    const endereco = [];
    for (let x = 0; x < listEnderecos.length; x += 1) {
      if (listEnderecos[x].id === id) {
        if (!listEnderecos[x].novo) {
          endereco.push({ ...listEnderecos[x], editando: false });
        }
      } else {
        endereco.push({ ...listEnderecos[x], editando: false });
      }
    }

    setListEnderecos(endereco);
  };

  const handleAdd = () => {
    if (funcionario.id !== '') {
      let permitir = true;

      for (let x = 0; x < listEnderecos.length; x += 1) {
        if (listEnderecos[x].endereco === '') {
          permitir = false;
        }
      }

      if (permitir) {
        const enderecos = [];

        for (let x = 0; x < listEnderecos.length; x += 1) {
          enderecos.push({
            id: listEnderecos[x].id,
            cep: listEnderecos[x].cep,
            endereco: listEnderecos[x].endereco,
            complemento: listEnderecos[x].complemento,
            bairro: listEnderecos[x].bairro,
            estado: listEnderecos[x].estado,
            cidade: listEnderecos[x].cidade,
            numero: listEnderecos[x].numero,
          });
        }

        enderecos.push({
          id: Math.random()
            .toString(36)
            .substring(7),
          cep: '',
          endereco: '',
          complemento: '',
          bairro: '',
          estado: '',
          cidade: '',
          numero: '',
          novo: true,
        });

        setListEnderecos(enderecos);
      } else {
        toast.error('Ops! preencha os campos corretamente antes de adicionar');
      }
    }
  };

  const handleSalvar = async id => {
    setErrosForm(null);
    let editando;
    const endereco = {
      id: '',
      cep: '',
      endereco: '',
      complemento: '',
      bairro: '',
      estado: '',
      cidade: '',
      numero: '',
    };

    try {
      for (let x = 0; x < listEnderecos.length; x += 1) {
        if (listEnderecos[x].id === id) {
          endereco.id = listEnderecos[x].id;
          endereco.cep = listEnderecos[x].cep;
          endereco.complemento = listEnderecos[x].complemento;
          endereco.bairro = listEnderecos[x].bairro;
          endereco.estado = listEnderecos[x].estado;
          endereco.cidade = listEnderecos[x].cidade;
          endereco.numero = listEnderecos[x].numero;
          endereco.endereco = listEnderecos[x].endereco;
          editando = listEnderecos[x].editando;
          break;
        }
      }

      const schema = Yup.object().shape({
        cep: Yup.number()
          .transform((cv, ov) => {
            return ov === '' ? undefined : cv;
          })
          .required('Informe o Cep'),
        endereco: Yup.string().required('Informe o Endereo'),
        complemento: Yup.string(),
        bairro: Yup.string().required('Informe o Bairro'),
        estado: Yup.string().required('informe o Estado'),
        cidade: Yup.string().required('Informe a Cidade'),
        numero: Yup.number().transform((cv, ov) => {
          return ov === '' ? undefined : cv;
        }),
      });
      await schema.validate(endereco, {
        abortEarly: false,
      });

      let retorno;
      if (editando) {
        retorno = await ServiceFuncionario.editarEndereco(
          id,
          funcionario.id,
          endereco
        );
      } else {
        retorno = await ServiceFuncionario.salvarEndereco(
          funcionario.id,
          endereco
        );
      }

      if (retorno && retorno.data.status) {
        const dataRetorno = retorno.data.data;

        const enderecos = [];

        for (let x = 0; x < listEnderecos.length; x += 1) {
          enderecos.push({
            id:
              listEnderecos[x].id === id && dataRetorno.id
                ? dataRetorno.id
                : listEnderecos[x].id,

            cep: listEnderecos[x].cep,
            endereco: listEnderecos[x].endereco,
            complemento: listEnderecos[x].complemento,
            bairro: listEnderecos[x].bairro,
            estado: listEnderecos[x].estado,
            cidade: listEnderecos[x].cidade,
            numero: listEnderecos[x].numero,

            novo: listEnderecos[x].id === id ? false : listEnderecos[x].novo,
            editando:
              listEnderecos[x].editando && listEnderecos[x].id === id
                ? false
                : listEnderecos[x].editando,
          });
        }

        if (editando) {
          toast.success('Endereo Atualizado com sucesso');
        } else {
          toast.success('Endereo salvo com sucesso');
        }
        setListEnderecos(enderecos);
      } else {
        toast.error(retorno.data.data.motivo);
      }
    } catch (err) {
      if (err instanceof Yup.ValidationError) {
        const errorMessages = {};
        err.inner.forEach(error => {
          errorMessages[error.path] = error.message;
        });
        setErrosForm(errorMessages);
        toast.error('Preencha os campos corretamente');
      } else {
        toast.error('Ocorreu um erro inesperado');
      }
    }
  };

  const handleExcluir = async id => {
    const retorno = await ServiceFuncionario.excluirEndereco(id);
    if (retorno && retorno.status) {
      const listaAux = [];

      for (let x = 0; x < listEnderecos.length; x += 1) {
        if (listEnderecos[x].id !== id) {
          listaAux.push(listEnderecos[x]);
        }
      }
      setListEnderecos(listaAux);
      toast.success('Endereo excluido com sucesso');
    }
  };

  return (
    <>
      {listEnderecos.length > 0 ? (
        listEnderecos.map(e =>
          e.editando || e.novo ? (
            <Content key={e.id}>
              <Container fluid>
                <Card>
                  <Card.Body>
                    <Row>
                      <Col md={8} xs={12}>
                        <InputText
                          id={e.id}
                          label="Endereo"
                          name="endereco"
                          value={e.endereco}
                          changeInput={changeInput}
                          errosForm={errosForm}
                        />
                      </Col>
                      <Col md={3} xs={12}>
                        <InputText
                          id={e.id}
                          label="Cep"
                          name="cep"
                          type="number"
                          value={e.cep}
                          changeInput={changeInput}
                          errosForm={errosForm}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col md={2} xs={12}>
                        <InputOptions
                          id={e.id}
                          label="Estado"
                          name="estado"
                          value={e.estado}
                          changeInput={changeInput}
                          Options={OptiosEstado}
                          errosForm={errosForm}
                        />
                      </Col>
                      <Col md={4} xs={12}>
                        <InputText
                          id={e.id}
                          label="Cidade"
                          name="cidade"
                          value={e.cidade}
                          errosForm={errosForm}
                          changeInput={changeInput}
                        />
                      </Col>
                      <Col md={6} xs={12}>
                        <InputText
                          id={e.id}
                          label="Bairro"
                          name="bairro"
                          errosForm={errosForm}
                          value={e.bairro}
                          changeInput={changeInput}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col md={6} xs={12}>
                        <InputText
                          id={e.id}
                          label="Complemento"
                          name="complemento"
                          value={e.complemento}
                          changeInput={changeInput}
                        />
                      </Col>
                      <Col md={4} xs={12}>
                        <InputText
                          id={e.id}
                          label="Nmero"
                          name="numero"
                          type="number"
                          value={e.numero}
                          changeInput={changeInput}
                        />
                      </Col>
                    </Row>
                    <Row className="botao">
                      <Col md={4} xs={12} className="botao">
                        <Button
                          className="icon"
                          title="Cancelar"
                          onClick={() => handleCancelar(e.id)}
                        >
                          <svg
                            width="1.5em"
                            height="1.5em"
                            viewBox="0 0 16 16"
                            className="bi bi-x"
                            fill="currentColor"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path
                              fillRule="evenodd"
                              d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"
                            />
                          </svg>
                        </Button>

                        <Button
                          className="icon"
                          title="Salvar"
                          onClick={() => handleSalvar(e.id)}
                        >
                          <svg
                            width="1em"
                            height="1em"
                            viewBox="0 0 16 16"
                            className="bi bi-check2"
                            fill="currentColor"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path
                              fillRule="evenodd"
                              d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0z"
                            />
                          </svg>
                        </Button>
                      </Col>
                    </Row>
                  </Card.Body>
                </Card>
              </Container>
            </Content>
          ) : (
            <Content key={e.id}>
              <Container fluid>
                <Card>
                  <Card.Body>
                    <Row>
                      <Col md={8} xs={12}>
                        <Form.Label>
                          <strong>Endereos:</strong>
                          {e.endereco}{' '}
                        </Form.Label>
                      </Col>
                      <Col md={3} xs={12}>
                        <Form.Label>
                          <strong>Cep:</strong>
                          {e.cep}
                        </Form.Label>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={2} xs={12}>
                        <Form.Label>
                          <strong>Estado:</strong> {e.estado}
                        </Form.Label>
                      </Col>
                      <Col md={4} xs={12}>
                        <Form.Label>
                          <strong>cidade:</strong> {e.cidade}
                        </Form.Label>
                      </Col>
                      <Col md={6} xs={12}>
                        <Form.Label>
                          <strong>Bairro:</strong> {e.bairro}
                        </Form.Label>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={6} xs={12}>
                        <Form.Label>
                          <strong>Complemento:</strong> {e.complemento}
                        </Form.Label>
                      </Col>
                      <Col md={4} xs={12}>
                        <Form.Label>
                          <strong>Nmero:</strong> {e.numero}
                        </Form.Label>
                      </Col>
                    </Row>
                    <Row className="botao">
                      <Col md={2} xs={12} className="botao">
                        <Button
                          className="icon"
                          title="Editar"
                          onClick={() => handleEditar(e.id)}
                        >
                          <svg
                            width="1em"
                            height="1em"
                            viewBox="0 0 16 16"
                            className="bi bi-pencil"
                            fill="currentColor"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path
                              fillRule="evenodd"
                              d="M11.293 1.293a1 1 0 0 1 1.414 0l2 2a1 1 0 0 1 0 1.414l-9 9a1 1 0 0 1-.39.242l-3 1a1 1 0 0 1-1.266-1.265l1-3a1 1 0 0 1 .242-.391l9-9zM12 2l2 2-9 9-3 1 1-3 9-9z"
                            />
                            <path
                              fillRule="evenodd"
                              d="M12.146 6.354l-2.5-2.5.708-.708 2.5 2.5-.707.708zM3 10v.5a.5.5 0 0 0 .5.5H4v.5a.5.5 0 0 0 .5.5H5v.5a.5.5 0 0 0 .5.5H6v-1.5a.5.5 0 0 0-.5-.5H5v-.5a.5.5 0 0 0-.5-.5H3z"
                            />
                          </svg>
                        </Button>
                        <Button
                          className="icon"
                          title="Exluir"
                          onClick={() =>
                            Modal.showModal(
                              'Excluir Endereo',
                              'Deseja realmente excluir este endereo?',

                              handleExcluir.bind(this, e.id)
                            )
                          }
                        >
                          <svg
                            width="1em"
                            height="1em"
                            viewBox="0 0 16 16"
                            className="bi bi-trash"
                            fill="currentColor"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                            <path
                              fillRule="evenodd"
                              d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
                            />
                          </svg>
                        </Button>
                      </Col>
                    </Row>
                  </Card.Body>
                </Card>
              </Container>
            </Content>
          )
        )
      ) : (
        <h5>no h Endereos</h5>
      )}
      <Row className="botao">
        <Col md={4} style={{ marginTop: 15 }} className="botao">
          <Button
            title="Novo endereo"
            className="plus"
            disabled={funcionario.id === '' ? 'disabled' : ''}
            onClick={() => handleAdd()}
          >
            <svg
              width="1em"
              height="1em"
              viewBox="0 0 16 16"
              className="bi bi-plus"
              fill="currentColor"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z"
              />
            </svg>
          </Button>
        </Col>
      </Row>
    </>
  );
}

ListaEnderecos.propTypes = {
  data: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
  funcionario: PropTypes.oneOfType([PropTypes.object]),
};
ListaEnderecos.defaultProps = {
  data: [],
  funcionario: {},
};
