import React from 'react';
// import { FiPlusCircle, FiMinusCircle } from 'react-icons/fi';
import Form from 'react-bootstrap/Form';
import TreeMenu from 'react-simple-tree-menu';
import dashboardRotas from '../../routes/dashboardRoutes';
import { Content, TituloDrawer, Itens } from './style';

export default function Drawer(props) {
  const { handleAbrirPagina, handleDrawer } = props;

  const handleClick = (props, toggleNode, hasNodes, e, path, showDrawer) => {
    if (hasNodes && toggleNode) {
      toggleNode();
    }
    e.stopPropagation();
    if (!hasNodes) {
      handleDrawer(false);
      handleAbrirPagina(path);
    }
  };

  /*
  const openedIcon = <FiPlusCircle />;
  const closedIcon = <FiMinusCircle />;
  */

  const levelMenu = (level, hasNodes) => {
    switch (level) {
      case 1:
        return 'subItensL1';

      case 2:
        return 'subItensL2';

      case 3:
        return 'subItensL3';

      default:
        return '';
    }
  };

  /*
  const iconMenu = (hasNodes, props) => {
    return hasNodes && (props.isOpen ? closedIcon : openedIcon);
  }; */

  return (
    <Content>
      <TituloDrawer>ERP WEB</TituloDrawer>
      <TreeMenu data={dashboardRotas} debounceTime={125}>
        {({ search, items }) => (
          <>
            <Form.Group controlId="formBasicEmail">
              <Form.Control
                type="text"
                placeholder="Pesquisar"
                onChange={e => search(e.target.value)}
              />
            </Form.Group>
            <Itens>
              {items.map(
                ({
                  key,
                  label,
                  hasNodes,
                  level,
                  path,
                  toggleNode,
                  showDrawer,
                  ...props
                }) =>
                  showDrawer !== false && (
                    <li
                      key={key}
                      onClick={e => {
                        handleClick(
                          props,
                          toggleNode,
                          hasNodes,
                          e,
                          path,
                          showDrawer
                        );
                      }}
                      className={levelMenu(level, hasNodes)}
                    >
                      {/* iconMenu(hasNodes, props) */}
                      {label}
                    </li>
                  )
              )}
            </Itens>
          </>
        )}
      </TreeMenu>
    </Content>
  );
}
