import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { toast } from 'react-toastify';
import * as Yup from 'yup';
// ----------formulario------
import {
  Container,
  Row,
  Col,
  Form,
  Button,
  Accordion,
  Card,
  Jumbotron,
} from 'react-bootstrap';
import InputMask from 'react-input-mask';
import { useDispatch } from 'react-redux';
import Modal from '../../../../components/modal/index';
import ListaTelefones from '../../../../components/listaTelefones/index';
import ListaEnderecos from '../../../../components/listaEnderecos/index';
import ListaDocumentos from '../../../../components/listaDocumentos/index';
import ListaEmails from '../../../../components/listaEmails/index';
import LabelErro from '../../../../components/labelError/index';
import HasError from '../../../../components/classErrorInput';
import InputText from '../../../../components/form/inputText';
import InputOptions from '../../../../components/form/inputOptions';
import InputDate from '../../../../components/form/inputDate';
//--------------------------

import { Content, ContentBotao } from './styles';

import Loading from '../../../../components/loading';
import { FuncionarioDTO } from '../../../../models/Funcionario';

import ServiceFuncionario from '../../../../services/serviceFuncionario';
import ServiceGlobal from '../../../../services/serviceGlobal';
import { logout } from '../../../../store/modules/auth/actions';

export default function CadastroFuncionario(props) {
  const dispatch = useDispatch();

  const { params, handleAbrirPagina } = props;
  const [paginaLoad, setPaginaLoad] = React.useState(true);

  // --------------------formulario----------------------
  const [funcionario, setFuncionario] = React.useState(FuncionarioDTO());
  const [errosForm, setErrosForm] = React.useState();
  const [listaSelects, setListaSelects] = React.useState(null);
  const [listTelefones, setListTelefones] = React.useState([]);
  const [listEnderecos, setListEnderecos] = React.useState([]);
  const [listDocumentos, setListDocumentos] = React.useState([]);
  const [listEmails, setListEmails] = React.useState([]);
  const [menuActive, setMenuActive] = React.useState('0');
  function changeInput(campo, e) {
    setFuncionario({ ...funcionario, [campo]: e.target.value });
  }
  async function handleSubmit(data) {
    data.preventDefault();
    setErrosForm(null);
    try {
      const schema = Yup.object().shape({
        nome: Yup.string().required('Informe o nome'),
        cpf: Yup.string().required('Informe o CPF'),
        rg: Yup.string().required('Informe o RG'),
        sexo_id: Yup.number()
          .transform((cv, ov) => {
            return ov === '' ? undefined : cv;
          })
          .required('Informe o Sexo'),
        data_nascimento: Yup.string(),
        estado_civil_id: Yup.number().transform((cv, ov) => {
          return ov === '' ? undefined : cv;
        }),
        escolaridade_id: Yup.number().transform((cv, ov) => {
          return ov === '' ? undefined : cv;
        }),
        data_admissao: Yup.string(),
        data_demissao: Yup.string(),
        cargo_id: Yup.number()
          .transform((cv, ov) => {
            return ov === '' ? undefined : cv;
          })
          .required('Informe o cargo'),
        num_carteira_trabalho: Yup.number().transform((cv, ov) => {
          return ov === '' ? undefined : cv;
        }),
        categoria_cnh_id: Yup.number().transform((cv, ov) => {
          return ov === '' ? undefined : cv;
        }),
        banco_id: Yup.number().transform((cv, ov) => {
          return ov === '' ? undefined : cv;
        }),
        numero_agencia: Yup.number().transform((cv, ov) => {
          return ov === '' ? undefined : cv;
        }),
        digito_agencia: Yup.number().transform((cv, ov) => {
          return ov === '' ? undefined : cv;
        }),
        conta: Yup.number().transform((cv, ov) => {
          return ov === '' ? undefined : cv;
        }),
        digito_conta: Yup.number().transform((cv, ov) => {
          return ov === '' ? undefined : cv;
        }),
        nome_favorecido: Yup.string(),
        cpf_favorecido: Yup.string(),
      });

      await schema.validate(funcionario, {
        abortEarly: false,
      });

      const editando = !!(
        funcionario &&
        funcionario.id &&
        funcionario.id !== ''
      );

      const retorno = await ServiceFuncionario.salvarEditarFuncionario(
        funcionario,
        editando
      );
      if (retorno.data.status) {
        if (!editando) {
          await setFuncionario(FuncionarioDTO(retorno.data.data));
          toast.success('Salvo com sucesso');
        } else {
          toast.success('Atualizado com sucesso');
        }
      } else {
        toast.error(retorno.data.motivo);
      }
    } catch (err) {
      if (err instanceof Yup.ValidationError) {
        const errorMessages = {};
        err.inner.forEach(error => {
          errorMessages[error.path] = error.message;
        });
        setErrosForm(errorMessages);
      }
    }
  }

  async function preencherSelects() {
    const selects = await ServiceGlobal.selects();

    if (selects && selects.data.status) {
      const { data } = selects.data;
      setListaSelects(data);
      return true;
    }
    toast.error(selects.data.data.motivo);
    if (selects.status === 401) {
      dispatch(logout());
    }
    return false;
  }

  // preenche selects
  async function preencheAuxiliares(data) {
    // preenche lista de telefones do funcionario
    if (data.telefones.length > 0) {
      const tels = [];
      data.telefones.map(t => {
        tels.push({ id: t.id, numero: t.numero, tipo_telefone_id: t.tipo.id });
      });
      setListTelefones(tels);
    }
    // preenche lista de enderecos do funcionario
    if (data.enderecos.length > 0) {
      const enderecos = [];
      data.enderecos.map(e => {
        enderecos.push({
          id: e.id,
          cep: e.cep,
          endereco: e.endereco,
          complemento: e.complemento,
          bairro: e.bairro,
          estado: e.estado,
          cidade: e.cidade,
          numero: e.numero,
        });
      });
      setListEnderecos(enderecos);
    }

    if (data.emails.length > 0) {
      const emails = [];
      data.emails.map(t => {
        emails.push({
          id: t.id,
          email: t.email,
          tipo_email_id: t.tipoEmail.id,
        });
      });
      setListEmails(emails);
    }

    if (data.documentos.length > 0) {
      const documentos = [];
      data.documentos.map(d => {
        documentos.push({
          id: d.id,
          nome: d.nome,
          descricao: d.descricao,
          tipo_documento_id: d.tipoDocumento.id,
          arquivos: d.arquivos,
        });
      });
      setListDocumentos(documentos);
    }
  }

  async function excluirFuncionario() {
    try {
      const excluir = !!(
        funcionario &&
        funcionario.id &&
        funcionario.id !== ''
      );

      if (excluir) {
        const retorno = await ServiceFuncionario.excluirFuncionario(
          funcionario.id
        );
        console.log(retorno);

        if (retorno.data.status) {
          toast.success('Funcionario removido com sucesso');
          handleAbrirPagina('/dashboard/funcionarios');
          return;
        }
        toast.error(retorno.data.data.motivo);
      }
    } catch (err) {
      toast.error('Houve um problema ao excluir');
    }
  }

  //-----------------------------------------------------

  useEffect(() => {
    async function ini() {
      setPaginaLoad(true);

      if (!(await preencherSelects())) {
        return;
      }
      if (params.get('cod')) {
        const retorno = await ServiceFuncionario.consultaFuncionario(
          params.get('cod')
        );
        if (retorno && retorno.status === 200) {
          await setFuncionario(FuncionarioDTO(retorno.data.data));
          await preencheAuxiliares(retorno.data.data);
        } else if (retorno.status === 401) {
          toast.error(retorno.data.data.motivo);
          dispatch(logout());
          return;
        } else {
          toast.error(retorno.data.data.motivo);
        }
      }
      setPaginaLoad(false);
    }
    ini();

    return () => {
      //---
    };
  }, []);

  return (
    <>
      {paginaLoad ? (
        <Loading />
      ) : (
        <Jumbotron fluid className="fundoAba">
          <Content>
            <Form onSubmit={handleSubmit} autoComplete="off">
              <Accordion
                defaultActiveKey="0"
                className="quadro"
                onSelect={e => setMenuActive(e)}
              >
                <Card
                  className={menuActive && menuActive === '0' && 'headerActive'}
                >
                  <Accordion.Toggle as={Card.Header} eventKey="0">
                    Informaes pessoais
                  </Accordion.Toggle>
                  <Accordion.Collapse eventKey="0">
                    <Card.Body>
                      <Container fluid>
                        <Row>
                          <Col md={2} xs={6}>
                            <InputText
                              label="Carteira de trabalho"
                              name="num_carteira_trabalho"
                              value={funcionario.num_carteira_trabalho}
                              changeInput={changeInput}
                              errosForm={errosForm}
                              type="number"
                            />
                          </Col>
                          <Col md={4} xs={12}>
                            <InputText
                              label="Nome"
                              name="nome"
                              maxLength={255}
                              value={funcionario.nome}
                              changeInput={changeInput}
                              errosForm={errosForm}
                            />
                          </Col>
                          <Col md={3} xs={12}>
                            <Form.Group>
                              <Form.Label>CPF</Form.Label>
                              <InputMask
                                mask="999.999.999-99"
                                name="cpf"
                                className={`form-control ${HasError(
                                  errosForm,
                                  'nome'
                                )}`}
                                onChange={e => changeInput('cpf', e)}
                                value={funcionario.cpf}
                              />
                              <LabelErro error={errosForm} name="cpf" />
                            </Form.Group>
                          </Col>
                          <Col md={3} xs={12}>
                            <Form.Group>
                              <Form.Label>RG</Form.Label>
                              <InputMask
                                mask="99.999.999-**"
                                name="rg"
                                className={`form-control ${HasError(
                                  errosForm,
                                  'rg'
                                )}`}
                                onChange={e => changeInput('rg', e)}
                                value={funcionario.rg}
                              />
                              <LabelErro error={errosForm} name="rg" />
                            </Form.Group>
                          </Col>
                        </Row>
                        <Row>
                          <Col md={3} xs={12}>
                            <Form.Group>
                              <Form.Label>Data de nascimento</Form.Label>
                              <Form.Control
                                type="date"
                                name="data_nascimento"
                                value={funcionario.data_nascimento}
                                className={`form-control ${HasError(
                                  errosForm,
                                  'data_nascimento'
                                )}`}
                                onChange={e =>
                                  changeInput('data_nascimento', e)
                                }
                              />
                              <LabelErro
                                error={errosForm}
                                name="data_nascimento"
                              />
                            </Form.Group>
                          </Col>
                          <Col md={3} xs={12}>
                            <Form.Label>Sexo</Form.Label>
                            <Form.Control
                              as="select"
                              name="sexo_id"
                              className={`form-control ${HasError(
                                errosForm,
                                'sexo_id'
                              )}`}
                              custom
                              value={funcionario.sexo_id}
                              onChange={e =>
                                setFuncionario({
                                  ...funcionario,
                                  sexo_id: parseInt(e.target.value, 10),
                                })
                              }
                            >
                              <option value="" key="selecione">
                                selecione
                              </option>
                              {listaSelects.sexos.map(s => (
                                <option value={s.id} key={s.id}>
                                  {s.nome}
                                </option>
                              ))}
                            </Form.Control>
                            <LabelErro error={errosForm} name="sexo_id" />
                          </Col>
                          <Col md={3} xs={12}>
                            <Form.Label>Estado civil</Form.Label>
                            <Form.Control
                              as="select"
                              name="estado_civil_id"
                              className={`form-control ${HasError(
                                errosForm,
                                'estado_civil_id'
                              )}`}
                              custom
                              value={funcionario.estado_civil_id}
                              onChange={e =>
                                setFuncionario({
                                  ...funcionario,
                                  estado_civil_id: parseInt(e.target.value, 10),
                                })
                              }
                            >
                              <option value="" key="selecione">
                                selecione
                              </option>
                              {listaSelects.estadoCivil.map(e => (
                                <option value={e.id} key={e.id}>
                                  {e.nome}
                                </option>
                              ))}
                            </Form.Control>
                            <LabelErro
                              error={errosForm}
                              name="estado_civil_id"
                            />
                          </Col>

                          <Col md={3} xs={12}>
                            <Form.Label>Categoria CNH</Form.Label>
                            <Form.Control
                              as="select"
                              name="categoria_cnh_id"
                              className={`form-control ${HasError(
                                errosForm,
                                'categoria_cnh_id'
                              )}`}
                              custom
                              value={funcionario.categoria_cnh_id}
                              onChange={e =>
                                setFuncionario({
                                  ...funcionario,
                                  categoria_cnh_id: parseInt(
                                    e.target.value,
                                    10
                                  ),
                                })
                              }
                            >
                              <option value="" key="selecione">
                                selecione
                              </option>
                              {listaSelects.categoriaCnh.map(e => (
                                <option value={e.id} key={e.id}>
                                  {e.nome}
                                </option>
                              ))}
                            </Form.Control>
                            <LabelErro
                              error={errosForm}
                              name="categoria_cnh_id"
                            />
                          </Col>
                        </Row>
                        <Row>
                          <Col md={3} xs={12}>
                            <Form.Label>Escolaridade</Form.Label>
                            <Form.Control
                              as="select"
                              name="escolaridade_id"
                              className={`form-control ${HasError(
                                errosForm,
                                'escolaridade_id'
                              )}`}
                              custom
                              value={funcionario.escolaridade_id}
                              onChange={e =>
                                setFuncionario({
                                  ...funcionario,
                                  escolaridade_id: parseInt(e.target.value, 10),
                                })
                              }
                            >
                              <option value="" key="selecione">
                                selecione
                              </option>
                              {listaSelects.escolaridade.map(e => (
                                <option value={e.id} key={e.id}>
                                  {e.nome}
                                </option>
                              ))}
                            </Form.Control>
                            <LabelErro
                              error={errosForm}
                              name="escolaridade_id"
                            />
                          </Col>

                          <Col md={3} xs={12}>
                            <Form.Label>Cargo</Form.Label>
                            <Form.Control
                              as="select"
                              name="cargo_id"
                              className={`form-control ${HasError(
                                errosForm,
                                'cargo_id'
                              )}`}
                              custom
                              value={funcionario.cargo_id}
                              onChange={e =>
                                setFuncionario({
                                  ...funcionario,
                                  cargo_id: parseInt(e.target.value, 10),
                                })
                              }
                            >
                              <option value="" key="selecione">
                                selecione
                              </option>
                              {listaSelects.cargo.map(e => (
                                <option value={e.id} key={e.id}>
                                  {e.nome}
                                </option>
                              ))}
                            </Form.Control>
                            <LabelErro error={errosForm} name="cargo_id" />
                          </Col>

                          <Col md={3} xs={12}>
                            <InputDate
                              label="Data admisso"
                              name="data_admissao"
                              value={funcionario.data_admissao}
                              changeInput={changeInput}
                              errosForm={errosForm}
                            />
                          </Col>
                          <Col md={3} xs={12}>
                            <InputDate
                              label="Data demisso"
                              name="data_demissao"
                              value={funcionario.data_demissao}
                              changeInput={changeInput}
                              errosForm={errosForm}
                            />
                          </Col>
                        </Row>

                        <Row />

                        <Row>
                          <Col md={2} xs={6}>
                            <Form.Group controlId="formBasicCheckbox">
                              <Form.Check
                                type="checkbox"
                                label="Mesma titularidade"
                              />
                            </Form.Group>
                          </Col>
                          <Col md={4} xs={12}>
                            <Form.Group>
                              <Form.Label>CPF favorecido</Form.Label>
                              <InputMask
                                mask="999.999.999-99"
                                name="cpf_favorecido"
                                className={`form-control ${HasError(
                                  errosForm,
                                  'cpf_favorecido'
                                )}`}
                                onChange={e => changeInput('cpf_favorecido', e)}
                                value={funcionario.cpf_favorecido}
                              />
                              <LabelErro
                                error={errosForm}
                                name="cpf_favorecido"
                              />
                            </Form.Group>
                          </Col>

                          <Col md={6} xs={12}>
                            <InputText
                              label="Nome favorecido"
                              name="nome_favorecido"
                              maxLength={255}
                              value={funcionario.nome_favorecido}
                              changeInput={changeInput}
                              errosForm={errosForm}
                            />
                          </Col>
                        </Row>

                        <Row>
                          <Col md={4} xs={12}>
                            <Form.Label>Banco</Form.Label>
                            <Form.Control
                              as="select"
                              name="banco_id"
                              className={`form-control ${HasError(
                                errosForm,
                                'banco_id'
                              )}`}
                              custom
                              value={funcionario.banco_id}
                              onChange={e =>
                                setFuncionario({
                                  ...funcionario,
                                  banco_id: parseInt(e.target.value, 10),
                                })
                              }
                            >
                              <option value="" key="selecione">
                                selecione
                              </option>
                              {listaSelects.banco.map(e => (
                                <option value={e.id} key={e.id}>
                                  {e.nome}
                                </option>
                              ))}
                            </Form.Control>
                            <LabelErro error={errosForm} name="banco_id" />
                          </Col>

                          <Col md={2} xs={6}>
                            <InputText
                              label="Agncia"
                              name="numero_agencia"
                              maxLength={20}
                              value={funcionario.numero_agencia}
                              changeInput={changeInput}
                              errosForm={errosForm}
                              type="number"
                            />
                          </Col>
                          <Col md={2} xs={6}>
                            <InputText
                              label="Dgito"
                              name="digito_agencia"
                              maxLength={20}
                              value={funcionario.digito_agencia}
                              changeInput={changeInput}
                              errosForm={errosForm}
                              type="number"
                            />
                          </Col>
                          <Col md={2} xs={5}>
                            <InputText
                              label="Conta"
                              name="conta"
                              maxLength={20}
                              value={funcionario.conta}
                              changeInput={changeInput}
                              errosForm={errosForm}
                              type="number"
                            />
                          </Col>
                          <Col md={2} xs={6}>
                            <InputText
                              label="Dgito"
                              name="digito_conta"
                              maxLength={20}
                              value={funcionario.digito_conta}
                              changeInput={changeInput}
                              errosForm={errosForm}
                              type="number"
                            />
                          </Col>
                        </Row>
                        <Row className="botao">
                          <ContentBotao>
                            <Button
                              type="submit"
                              variant="success"
                              className="plus"
                              title="Salvar informaes"
                            >
                              <svg
                                width="1em"
                                height="1em"
                                viewBox="0 0 16 16"
                                className="bi bi-check2"
                                fill="currentColor"
                                xmlns="http://www.w3.org/2000/svg"
                              >
                                <path
                                  fillRule="evenodd"
                                  d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0z"
                                />
                              </svg>
                            </Button>
                          </ContentBotao>
                        </Row>
                      </Container>
                    </Card.Body>
                  </Accordion.Collapse>
                </Card>
                <Card
                  className={`${
                    menuActive === '1' ? 'headerActive' : ''
                  } ${funcionario.id === '' && 'disabled'}`}
                >
                  <Accordion.Toggle as={Card.Header} eventKey="1">
                    Telefones
                  </Accordion.Toggle>
                  <Accordion.Collapse eventKey="1">
                    <Card.Body>
                      <ListaTelefones
                        data={listTelefones}
                        tipo={listaSelects.tipoTelefone}
                        funcionario={funcionario}
                      />
                    </Card.Body>
                  </Accordion.Collapse>
                </Card>
                <Card
                  className={`${
                    menuActive === '2' ? 'headerActive' : ''
                  } ${funcionario.id === '' && 'disabled'}`}
                >
                  <Accordion.Toggle as={Card.Header} eventKey="2">
                    E-mails
                  </Accordion.Toggle>
                  <Accordion.Collapse eventKey="2">
                    <Card.Body>
                      <ListaEmails
                        data={listEmails}
                        tipo={listaSelects.tipoEmail}
                        funcionario={funcionario}
                      />
                    </Card.Body>
                  </Accordion.Collapse>
                </Card>
                <Card
                  className={`${
                    menuActive === '3' ? 'headerActive' : ''
                  } ${funcionario.id === '' && 'disabled'}`}
                >
                  <Accordion.Toggle as={Card.Header} eventKey="3">
                    Endereos
                  </Accordion.Toggle>
                  <Accordion.Collapse eventKey="3">
                    <Card.Body>
                      <ListaEnderecos
                        data={listEnderecos}
                        funcionario={funcionario}
                      />
                    </Card.Body>
                  </Accordion.Collapse>
                </Card>
                <Card
                  className={`${
                    menuActive === '4' ? 'headerActive' : ''
                  } ${funcionario.id === '' && 'disabled'}`}
                >
                  <Accordion.Toggle as={Card.Header} eventKey="4">
                    Documentos
                  </Accordion.Toggle>
                  <Accordion.Collapse eventKey="4">
                    <Card.Body>
                      <ListaDocumentos
                        funcionario={funcionario}
                        data={listDocumentos}
                        tipo={listaSelects.tipoDocumento}
                      />
                    </Card.Body>
                  </Accordion.Collapse>
                </Card>
              </Accordion>

              <ContentBotao>
                <Row className="botao">
                  <Col md={3} xs={12} className="botao">
                    <Button
                      className="sec"
                      onClick={() =>
                        Modal.showModal(
                          'Excluir Funcionrio',
                          'Deseja realmente excluir este funcinrio?',
                          excluirFuncionario
                        )
                      }
                    >
                      Cancelar
                    </Button>
                    <Button type="submit" className="principal">
                      Salvar
                    </Button>
                  </Col>
                </Row>
              </ContentBotao>
            </Form>
          </Content>
        </Jumbotron>
      )}
    </>
  );
}

CadastroFuncionario.propTypes = {
  params: PropTypes.oneOfType([PropTypes.element, PropTypes.func]).isRequired,
};
