import React from 'react';
import PropTypes from 'prop-types';
import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import InputMask from 'react-input-mask';
import { toast } from 'react-toastify';
import * as Yup from 'yup';
import ServiceFuncionario from '../../services/serviceFuncionario';
import { Line, Content, BlocoBottom } from './style';
import Modal from '../modal/index';

export default function ListaTelefones(props) {
  const { data, tipo, funcionario } = props;
  const [listTelefones, setListTelefones] = React.useState(data);
  const [errosForm, setErrosForm] = React.useState();

  const handleSalvar = async id => {
    setErrosForm(null);

    let editando;

    const telefone = {
      tipo_telefone_id: '',
      numero: '',
    };

    try {
      for (let x = 0; x < listTelefones.length; x += 1) {
        if (listTelefones[x].id === id) {
          telefone.numero = listTelefones[x].numero.replace(/[^\d]/g, '');
          telefone.tipo_telefone_id = listTelefones[x].tipo_telefone_id;
          editando = listTelefones[x].editando;
          break;
        }
      }

      const schema = Yup.object().shape({
        tipo_telefone_id: Yup.number().required(),
        numero: Yup.number()
          .min(10)
          .required(),
      });
      await schema.validate(telefone, {
        abortEarly: false,
      });

      let retorno;
      if (editando) {
        retorno = await ServiceFuncionario.editarTelefone(
          id,
          parseInt(telefone.tipo_telefone_id),
          telefone.numero
        );
      } else {
        retorno = await ServiceFuncionario.salvarTelefone(
          funcionario.id,
          parseInt(telefone.tipo_telefone_id),
          telefone.numero
        );
      }

      if (retorno && retorno.data.status) {
        const dataRetorno = retorno.data.data;

        const tels = [];

        for (let x = 0; x < listTelefones.length; x += 1) {
          tels.push({
            id:
              listTelefones[x].id === id && dataRetorno.id
                ? dataRetorno.id
                : listTelefones[x].id,
            numero: listTelefones[x].numero,
            tipo_telefone_id: listTelefones[x].tipo_telefone_id,
            novo: listTelefones[x].id === id ? false : listTelefones[x].novo,
            editando:
              listTelefones[x].editando && listTelefones[x].id === id
                ? false
                : listTelefones[x].editando,
          });
        }

        setListTelefones(tels);
        if (editando) {
          toast.success('Telefone Atualizado com sucesso');
        } else {
          toast.success('Telefone salvo com sucesso');
        }
      } else {
        toast.error(retorno.data.data.motivo);
      }
    } catch (err) {
      if (err instanceof Yup.ValidationError) {
        const errorMessages = {};
        err.inner.forEach(error => {
          errorMessages[error.path] = { id, menssagem: error.message };
        });
        setErrosForm(errorMessages);
        toast.error('Preencha os campos corretamente');
      } else {
        toast.error('Ocorreu um erro inesperado');
      }
    }
  };

  const handleExcluir = async id => {
    const retorno = await ServiceFuncionario.excluirTelefone(id);
    if (retorno && retorno.status) {
      const listaAux = [];

      for (let x = 0; x < listTelefones.length; x += 1) {
        if (listTelefones[x].id !== id) {
          listaAux.push(listTelefones[x]);
        }
      }
      setListTelefones(listaAux);
    }
  };

  const handleAdd = () => {
    if (funcionario.id !== '') {
      let permitir = true;

      for (let x = 0; x < listTelefones.length; x += 1) {
        if (listTelefones[x].numero === '') permitir = false;
      }

      if (permitir) {
        const tels = [];

        for (let x = 0; x < listTelefones.length; x += 1) {
          tels.push({
            id: listTelefones[x].id,
            numero: listTelefones[x].numero,
            tipo_telefone_id: listTelefones[x].tipo_telefone_id,
          });
        }

        tels.push({
          id: Math.random()
            .toString(36)
            .substring(7),
          numero: '',
          tipo_telefone_id: '',
          novo: true,
        });

        setListTelefones(tels);
      } else {
        toast.error('Ops! preencha os campos corretamente antes de adicionar');
      }
    }
  };

  function changeInput(key, campo, e) {
    setListTelefones(
      listTelefones.map(item =>
        item.id === key ? { ...item, [campo]: e.target.value } : item
      )
    );
  }

  const handleEditar = id => {
    setListTelefones(
      listTelefones.map(item =>
        item.id === id ? { ...item, editando: true } : item
      )
    );
  };

  return (
    <>
      {listTelefones.length > 0 ? (
        listTelefones.map(t => (
          <Content key={t.id}>
            <Container fluid>
              <Row>
                <Col md={4} xs={12}>
                  <Form.Group>
                    <Form.Label>Telefone</Form.Label>
                    <InputMask
                      mask="(99) 99999-9999 "
                      name="numero"
                      value={t.numero}
                      onChange={e => changeInput(t.id, 'numero', e)}
                      disabled={!t.editando && !t.novo}
                      className={`form-control ${
                        errosForm &&
                        errosForm.numero &&
                        errosForm.numero.id === t.id
                          ? 'input-error'
                          : ''
                      }`}
                    />
                  </Form.Group>
                </Col>
                <Col md={4} xs={12}>
                  <Form.Label>Tipo</Form.Label>
                  <Form.Control
                    as="select"
                    name="tipo_telefone_id"
                    custom
                    value={t.tipo_telefone_id}
                    onChange={e => changeInput(t.id, 'tipo_telefone_id', e)}
                    disabled={!t.editando && !t.novo}
                    className={`form-control ${
                      errosForm &&
                      errosForm.tipo_telefone_id &&
                      errosForm.tipo_telefone_id.id === t.id
                        ? 'input-error'
                        : ''
                    }`}
                  >
                    <option value="selecione" key="selecione">
                      selecione
                    </option>
                    {tipo.map(e => (
                      <option value={e.id} key={e.id}>
                        {e.tipo}
                      </option>
                    ))}
                  </Form.Control>
                </Col>
                <Col md={1} xs={12}>
                  <div className="botao">
                    {!t.editando && !t.novo && (
                      <Button
                        className="icon"
                        title="Editar"
                        onClick={() => handleEditar(t.id)}
                      >
                        <svg
                          width="1em"
                          height="1em"
                          viewBox="0 0 16 16"
                          className="bi bi-pencil"
                          fill="currentColor"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            fillRule="evenodd"
                            d="M11.293 1.293a1 1 0 0 1 1.414 0l2 2a1 1 0 0 1 0 1.414l-9 9a1 1 0 0 1-.39.242l-3 1a1 1 0 0 1-1.266-1.265l1-3a1 1 0 0 1 .242-.391l9-9zM12 2l2 2-9 9-3 1 1-3 9-9z"
                          />
                          <path
                            fillRule="evenodd"
                            d="M12.146 6.354l-2.5-2.5.708-.708 2.5 2.5-.707.708zM3 10v.5a.5.5 0 0 0 .5.5H4v.5a.5.5 0 0 0 .5.5H5v.5a.5.5 0 0 0 .5.5H6v-1.5a.5.5 0 0 0-.5-.5H5v-.5a.5.5 0 0 0-.5-.5H3z"
                          />
                        </svg>
                      </Button>
                    )}
                    {(t.novo || t.editando) && (
                      <Button
                        className="icon"
                        title="Confirmar"
                        variant="success"
                        onClick={() => handleSalvar(t.id)}
                      >
                        <svg
                          width="1em"
                          height="1em"
                          viewBox="0 0 16 16"
                          className="bi bi-check2"
                          fill="currentColor"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            fillRule="evenodd"
                            d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0z"
                          />
                        </svg>
                      </Button>
                    )}

                    {!t.novo && (
                      <Button
                        className="icon"
                        title="Excluir"
                        onClick={() =>
                          Modal.showModal(
                            'Excluir Telefone',
                            'Deseja realmente excluir este telefone?',

                            handleExcluir.bind(this, t.id)
                          )
                        }
                      >
                        <svg
                          width="1em"
                          height="1em"
                          viewBox="0 0 16 16"
                          className="bi bi-trash"
                          fill="currentColor"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                          <path
                            fillRule="evenodd"
                            d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
                          />
                        </svg>
                      </Button>
                    )}
                  </div>
                </Col>
              </Row>
              <Line />
            </Container>
          </Content>
        ))
      ) : (
        <h5>no h telefones</h5>
      )}
      <BlocoBottom className="botao">
        <Row>
          <Col md={4} xs={12} className="bt-adicionar-telefone">
            <Button
              variant="success"
              onClick={() => handleAdd()}
              className="plus"
              disabled={funcionario.id === '' ? 'disabled' : ''}
              title="Novo telefone"
            >
              <svg
                width="1em"
                height="1em"
                viewBox="0 0 16 16"
                className="bi bi-plus"
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z"
                />
              </svg>
            </Button>
          </Col>
        </Row>
      </BlocoBottom>
    </>
  );
}

ListaTelefones.propTypes = {
  data: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
  tipo: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
  funcionario: PropTypes.oneOfType([PropTypes.object]),
};
ListaTelefones.defaultProps = {
  data: [],
  tipo: [],
  funcionario: {},
};
