import { takeLatest, call, put, all } from 'redux-saga/effects';
import { toast } from 'react-toastify';
import { loginSucess, loginFailure, logoutSuccess } from './actions';
import api from '../../../services/api';
import History from '../../../services/history';

export function* login({ payload }) {
  try {
    const { email, senha } = payload;

    const { data } = yield call(api.post, 'sessao/login', {
      email,
      senha,
    });

    if (!data.status) {
      toast.error(data.data.motivo);
      yield put(loginFailure());
      return;
    }

    const { token, usuario } = data.data;
    // seta o token para ser enviado a todas requisies
    api.defaults.headers.Authorization = token;
    yield put(loginSucess(token, usuario));
    History.push('/dashboard');
  } catch (e) {
    toast.error('Falha na autenticao, verifique seus dados. ');
    yield put(loginFailure());
  }
}

export function setToken({ payload }) {
  // caso o usurio no tenha nada salvo
  if (!payload) return;

  const { token } = payload.auth;

  if (token) {
    api.defaults.headers.Authorization = token;
  }
}

export function* logout() {
  try {
    yield call(api.get, 'sessao/logout');
    yield put(logoutSuccess());
    History.push('/');
  } catch (e) {
    /* if (e.message.includes('406')) {
      yield put(logoutSuccess());
      History.push('/');
      return;
    }
    toast.error('Falha no logout. ');
    yield put(logoutFailure()); */
    yield put(logoutSuccess());
    History.push('/');
  }
}

export default all([
  // utilizando a action do redux-persist para pegar o token salvo
  takeLatest('persist/REHYDRATE', setToken),
  takeLatest('@auth/LOGIN_REQUEST', login),
  takeLatest('@auth/LOGOUT', logout),
]);
