import React, { useEffect } from 'react';

import Sidebar from 'react-sidebar';
import { useDispatch } from 'react-redux';
import { toast } from 'react-toastify';
import LoaderContent from '../../components/loaderContent';
import History from '../../services/history';
import { ContentDashboard, Content } from './styles';
import Drawer from '../../components/drawer/index';
import { stylesComponent } from '../../components/drawer/style';
import api from '../../services/api';
import { logout } from '../../store/modules/auth/actions';
import Loading from '../../components/loading';

const mql = window.matchMedia(`(min-width: 800px)`);

export default function Dashboard() {
  const dispatch = useDispatch();
  const [sidebarOpen, setSidebarOpen] = React.useState(false);
  const [sidebarDocked, setSidebarDocked] = React.useState(mql.matches);
  const [paginaLoad, setPaginaLoad] = React.useState(true);

  function handleLogout() {
    dispatch(logout());
  }

  const getRoute = () => {
    return window.location.pathname;
  };

  const getParams = () => {
    return new URLSearchParams(window.location.search);
  };

  const handleAbrirPagina = path => {
    History.push(path);
  };

  const mediaQueryChanged = () => {
    setSidebarOpen(false);
    setSidebarDocked(mql.matches);
  };

  const onSetSidebarOpen = open => {
    setSidebarOpen(open);
  };

  const verificaToken = async () => {
    const retorno = await api
      .get('sessao/verify')
      .then(r => {
        if (r.status) {
          return true;
        }
        return false;
      })
      .catch(err => {
        return false;
      });

    return retorno;
  };

  useEffect(() => {
    async function ini() {
      setPaginaLoad(true);
      const retorno = await verificaToken();

      if (!retorno) {
        toast.error('Sesso expirada.');
        handleLogout();
        return;
      }
      setPaginaLoad(false);
      mql.addListener(mediaQueryChanged);
    }
    ini();

    return () => {
      mql.removeListener(mediaQueryChanged);
    };
  }, []);

  return (
    <ContentDashboard>
      {paginaLoad ? (
        <Loading />
      ) : (
        <Content>
          <Sidebar
            styles={stylesComponent}
            sidebar={
              <>
                <Drawer
                  handleAbrirPagina={handleAbrirPagina}
                  handleDrawer={onSetSidebarOpen}
                />
              </>
            }
            shadow={false}
            open={sidebarOpen}
            docked={sidebarDocked}
            onSetOpen={onSetSidebarOpen}
          >
            <LoaderContent
              path={getRoute()}
              params={getParams()}
              handleAbrirPagina={handleAbrirPagina}
              handleDrawer={onSetSidebarOpen}
              sidebarDocked={sidebarDocked}
            />
          </Sidebar>
        </Content>
      )}
    </ContentDashboard>
  );
}
