import React from 'react';
import { Form } from 'react-bootstrap';
import LabelErro from '../labelError/index';
import HasError from '../classErrorInput';

// import { Container } from './styles';

function InputDate(props) {
  const { id, label, name, errosForm, value, changeInput } = props;
  return (
    <Form.Group>
      <Form.Label>{label}</Form.Label>

      {name && changeInput && (
        <>
          <Form.Control
            name={name}
            type="date"
            value={value}
            className={HasError(errosForm, name)}
            onChange={e =>
              id ? changeInput(id, name, e) : changeInput(name, e)
            }
          />
          {errosForm && <LabelErro error={errosForm} name={name} />}
        </>
      )}
    </Form.Group>
  );
}

export default InputDate;
