import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { Table, Button, Row, Col, Jumbotron, Form } from 'react-bootstrap';
import { toast } from 'react-toastify';
import { useDispatch } from 'react-redux';
import Pagination from 'react-js-pagination';
import { Content } from './styles';
import ServiceFuncionario from '../../../services/serviceFuncionario';
import { logout } from '../../../store/modules/auth/actions';
import Loading from '../../../components/loading';

export default function Funcionario(props) {
  const dispatch = useDispatch();
  const { handleAbrirPagina } = props;
  const [funcionario, setFuncionario] = React.useState();
  const [paginacao, setPaginacao] = React.useState({
    total_registros: 0,
    quantidade_paginas: 0,
    pagina_atual: 1,
    quantidade_na_pagina: 0,
  });
  const [inputBusca, setInputBusca] = React.useState('');
  const [paginaLoad, setPaginaLoad] = React.useState(true);

  async function buscaLista(busca = '', pagina = 1) {
    setPaginaLoad(true);
    const retorno = await ServiceFuncionario.listaFuncionario(busca, pagina);
    if (retorno && retorno.status === 200) {
      await setFuncionario(retorno.data.data.funcionarios);
      await setPaginacao(retorno.data.data.paginacao);
    } else {
      toast.error(retorno.data.data.motivo);
      dispatch(logout());
    }
    setPaginaLoad(false);
  }

  useEffect(() => {
    buscaLista();
  }, []);

  function formataData(data) {
    if (data) {
      const dataAux = data.split('-');
      return `${dataAux[2]}/${dataAux[1]}/${dataAux[0]}`;
    }
    return ' - ';
  }

  function handlePageChange(pageNumber) {
    setPaginacao({ ...paginacao, pagina_atual: pageNumber });
    buscaLista(inputBusca, pageNumber);
  }

  return (
    <>
      {paginaLoad ? (
        <Loading />
      ) : (
        <Jumbotron fluid className="fundo">
          <Content>
            <h4>Lista de Funcionarios</h4>

            <Row>
              <Col md={11} xs={12}>
                <Form.Group>
                  <Form.Label>Busca</Form.Label>
                  <Form.Control
                    name="busca"
                    maxLength={100}
                    value={inputBusca}
                    onChange={e => setInputBusca(e.target.value)}
                  />
                </Form.Group>
              </Col>

              <Col md={1} xs={12} className="botao">
                <Button className="icon" title="Pesquisar">
                  <svg
                    width="1em"
                    height="1em"
                    viewBox="0 0 16 16"
                    className="bi bi-search"
                    fill="currentColor"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fillRule="evenodd"
                      d="M10.442 10.442a1 1 0 0 1 1.415 0l3.85 3.85a1 1 0 0 1-1.414 1.415l-3.85-3.85a1 1 0 0 1 0-1.415z"
                    />
                    <path
                      fillRule="evenodd"
                      d="M6.5 12a5.5 5.5 0 1 0 0-11 5.5 5.5 0 0 0 0 11zM13 6.5a6.5 6.5 0 1 1-13 0 6.5 6.5 0 0 1 13 0z"
                    />
                  </svg>
                </Button>
                <Button className="icon" title="Limpar">
                  <svg
                    width="1em"
                    height="1em"
                    viewBox="0 0 16 16"
                    className="bi bi-trash"
                    fill="currentColor"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                    <path
                      fillRule="evenodd"
                      d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
                    />
                  </svg>
                </Button>
              </Col>
            </Row>
            <Row>
              <Col md={12} xs={12}>
                <Table striped bordered hover>
                  <thead>
                    <tr>
                      <th>Nome do funcionrio</th>
                      <th>CPF</th>
                      <th>RG</th>
                      <th>Data admisso</th>
                      <th />
                    </tr>
                  </thead>
                  <tbody>
                    {funcionario &&
                      funcionario.length > 0 &&
                      funcionario.map(f => (
                        <tr key={f.id}>
                          <td>{f.nome}</td>
                          <td>{f.cpf}</td>
                          <td>{f.rg}</td>
                          <td>{formataData(f.data_admissao)}</td>
                          <td className="icones">
                            <Row>
                              <Col md={12} xs={12} className="botao">
                                <Button
                                  variant="dark"
                                  onClick={() =>
                                    handleAbrirPagina(
                                      `/dashboard/funcionarios/cadastro?cod=${f.id}`
                                    )
                                  }
                                  className="icon_table"
                                  title="Editar"
                                >
                                  <svg
                                    width="1em"
                                    height="1em"
                                    viewBox="0 0 16 16"
                                    className="bi bi-pencil"
                                    fill="currentColor"
                                    xmlns="http://www.w3.org/2000/svg"
                                  >
                                    <path
                                      fillRule="evenodd"
                                      d="M11.293 1.293a1 1 0 0 1 1.414 0l2 2a1 1 0 0 1 0 1.414l-9 9a1 1 0 0 1-.39.242l-3 1a1 1 0 0 1-1.266-1.265l1-3a1 1 0 0 1 .242-.391l9-9zM12 2l2 2-9 9-3 1 1-3 9-9z"
                                    />
                                    <path
                                      fillRule="evenodd"
                                      d="M12.146 6.354l-2.5-2.5.708-.708 2.5 2.5-.707.708zM3 10v.5a.5.5 0 0 0 .5.5H4v.5a.5.5 0 0 0 .5.5H5v.5a.5.5 0 0 0 .5.5H6v-1.5a.5.5 0 0 0-.5-.5H5v-.5a.5.5 0 0 0-.5-.5H3z"
                                    />
                                  </svg>
                                </Button>
                              </Col>
                            </Row>
                          </td>
                        </tr>
                      ))}
                  </tbody>
                </Table>
              </Col>
            </Row>
            <Row>
              <Col md={12} xs={12}>
                <Pagination
                  itemClass="page-item"
                  linkClass="page-link"
                  activePage={paginacao.pagina_atual}
                  itemsCountPerPage={2}
                  totalItemsCount={paginacao.total_registros}
                  onChange={n => handlePageChange(n)}
                />
              </Col>
            </Row>
            <Row className="botao">
              <Col md={2} xs={12} className="botao">
                <Button
                  type="submit"
                  className="principal"
                  onClick={() =>
                    handleAbrirPagina('/dashboard/funcionarios/cadastro')
                  }
                >
                  Novo
                </Button>
              </Col>
            </Row>
          </Content>
        </Jumbotron>
      )}
    </>
  );
}

Funcionario.propTypes = {
  handleAbrirPagina: PropTypes.oneOfType([PropTypes.element, PropTypes.func])
    .isRequired,
};
