import React from 'react';
import PropTypes from 'prop-types';

import dashboardRotas from '../routes/dashboardRoutes';
import Header from './header/index';
import NotFound from '../pages/notFound';

export default function LoaderContent(props) {
  const {
    path,
    params,
    handleAbrirPagina,
    handleDrawer,
    sidebarDocked,
  } = props;

  function verificaNodes(lista) {
    let listNodes;

    for (let i = 0; i < lista.length; i += 1) {
      if (lista[i].Component && lista[i].path === path) {
        listNodes = lista[i].Component;
        break;
      }
      if (lista[i].nodes && lista[i].nodes.length > 0) {
        const retorno = verificaNodes(lista[i].nodes);
        if (retorno) {
          listNodes = retorno;
          break;
        }
      }
    }
    return listNodes;
  }

  const Component = verificaNodes(dashboardRotas);

  const divStyle = {
    marginTop: 70,
  };

  return (
    <>
      <Header handleDrawer={handleDrawer} sidebarDocked={sidebarDocked} />
      <div style={divStyle}>
        {Component ? (
          <Component params={params} handleAbrirPagina={handleAbrirPagina} />
        ) : (
          <NotFound handleAbrirPagina={handleAbrirPagina} />
        )}
      </div>
    </>
  );
}

LoaderContent.propTypes = {
  path: PropTypes.string,
  handleDrawer: PropTypes.oneOfType([PropTypes.element, PropTypes.func])
    .isRequired,
  sidebarDocked: PropTypes.bool.isRequired,
  handleAbrirPagina: PropTypes.oneOfType([PropTypes.element, PropTypes.func])
    .isRequired,
  params: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.func,
    PropTypes.object,
  ]),
};

LoaderContent.defaultProps = {
  path: '/dashboard',
  params: new URLSearchParams(window.location.search),
};
