import styled from 'styled-components';

export const Content = styled.div`
  margin: 12px 0;
  .cardItens {
    max-height: 170px;
    overflow: auto;

    .cards-files {
      .card {
        margin-bottom: 10px;
        width: 200px;
        .card-header {
          width: 100%;
          display: flex;
          justify-content: center;
        }
      }
    }
  }
`;

export const BlocoBottom = styled.div`
  .bt-adicionar-documento {
    margin-top: 15px;
    margin-left: 15px;

    @media (max-width: 500px) {
      display: flex;
      justify-content: center;
      margin-left: 0;
    }
  }
`;
