import React from 'react';
import PropTypes from 'prop-types';
import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import { toast } from 'react-toastify';
import * as Yup from 'yup';
import ServiceFuncionario from '../../services/serviceFuncionario';
import { Line, Content, BlocoBottom } from './style';
import Modal from '../modal/index';
import InputText from '../form/inputText';

export default function ListaEmails(props) {
  const { data, tipo, funcionario } = props;
  const [listEmails, setListEmails] = React.useState(data);
  const [errosForm, setErrosForm] = React.useState();

  const handleSalvar = async id => {
    setErrosForm(null);

    let editando;

    const email = {
      tipo_email_id: '',
      email: '',
    };

    try {
      for (let x = 0; x < listEmails.length; x += 1) {
        if (listEmails[x].id === id) {
          email.email = listEmails[x].email;
          email.tipo_email_id = listEmails[x].tipo_email_id;
          editando = listEmails[x].editando;
          break;
        }
      }

      const schema = Yup.object().shape({
        tipo_email_id: Yup.number().required(),
        email: Yup.string()
          .email()
          .required(),
      });

      await schema.validate(email, {
        abortEarly: false,
      });

      let retorno;
      if (editando) {
        retorno = await ServiceFuncionario.editarEmail(
          id,
          parseInt(email.tipo_email_id),
          email.email
        );
      } else {
        retorno = await ServiceFuncionario.salvarEmail(
          funcionario.id,
          parseInt(email.tipo_email_id),
          email.email
        );
      }

      if (retorno && retorno.data.status) {
        const dataRetorno = retorno.data.data;

        const emails = [];

        for (let x = 0; x < listEmails.length; x += 1) {
          emails.push({
            id:
              listEmails[x].id === id && dataRetorno.id
                ? dataRetorno.id
                : listEmails[x].id,
            email: listEmails[x].email,
            tipo_email_id: listEmails[x].tipo_email_id,
            novo: listEmails[x].id === id ? false : listEmails[x].novo,
            editando:
              listEmails[x].editando && listEmails[x].id === id
                ? false
                : listEmails[x].editando,
          });
        }

        setListEmails(emails);

        if (editando) {
          toast.success('Email Atualizado com sucesso');
        } else {
          toast.success('Email salvo com sucesso');
        }
      } else {
        toast.error(retorno.data.data.motivo);
      }
    } catch (err) {
      if (err instanceof Yup.ValidationError) {
        const errorMessages = {};
        err.inner.forEach(error => {
          errorMessages[error.path] = { id, menssagem: error.message };
        });
        setErrosForm(errorMessages);
        toast.error('Preencha os campos corretamente');
      } else {
        toast.error('Ocorreu um erro inesperado');
      }
    }
  };

  const handleExcluir = async id => {
    const retorno = await ServiceFuncionario.excluirEmail(id);
    if (retorno && retorno.status) {
      const listaAux = [];

      for (let x = 0; x < listEmails.length; x += 1) {
        if (listEmails[x].id !== id) {
          listaAux.push(listEmails[x]);
        }
      }
      setListEmails(listaAux);
    }
  };

  const handleAdd = () => {
    if (funcionario.id !== '') {
      let permitir = true;

      for (let x = 0; x < listEmails.length; x += 1) {
        if (listEmails[x].numero === '') permitir = false;
      }

      if (permitir) {
        const emails = [];

        for (let x = 0; x < listEmails.length; x += 1) {
          emails.push({
            id: listEmails[x].id,
            email: listEmails[x].email,
            tipo_email_id: listEmails[x].tipo_email_id,
          });
        }

        emails.push({
          id: Math.random()
            .toString(36)
            .substring(7),
          email: '',
          tipo_email_id: '',
          novo: true,
        });

        setListEmails(emails);
      } else {
        toast.error('Ops! preencha os campos corretamente antes de adicionar');
      }
    }
  };

  function changeInput(key, campo, e) {
    setListEmails(
      listEmails.map(item =>
        item.id === key ? { ...item, [campo]: e.target.value } : item
      )
    );
  }

  const handleEditar = id => {
    setListEmails(
      listEmails.map(item =>
        item.id === id ? { ...item, editando: true } : item
      )
    );
  };

  return (
    <>
      {listEmails.length > 0 ? (
        listEmails.map(t => (
          <Content key={t.id}>
            <Container fluid>
              <Row>
                <Col md={4} xs={12}>
                  <InputText
                    id={t.id}
                    label="Email"
                    name="email"
                    maxLength={255}
                    value={t.email}
                    changeInput={changeInput}
                    errosForm={errosForm}
                    disabled={!t.editando && !t.novo}
                  />
                </Col>
                <Col md={4} xs={12}>
                  <Form.Label>Tipo</Form.Label>
                  <Form.Control
                    as="select"
                    name="tipo_email_id"
                    custom
                    value={t.tipo_email_id}
                    onChange={e => changeInput(t.id, 'tipo_email_id', e)}
                    disabled={!t.editando && !t.novo}
                    className={`form-control ${
                      errosForm &&
                      errosForm.tipo_email_id &&
                      errosForm.tipo_email_id.id === t.id
                        ? 'input-error'
                        : ''
                    }`}
                  >
                    <option value="selecione" key="selecione">
                      selecione
                    </option>
                    {tipo.map(e => (
                      <option value={e.id} key={e.id}>
                        {e.tipo}
                      </option>
                    ))}
                  </Form.Control>
                </Col>
                <Col md={1} xs={12}>
                  <div className="botao">
                    {!t.editando && !t.novo && (
                      <Button
                        className="icon"
                        title="Editar"
                        onClick={() => handleEditar(t.id)}
                      >
                        <svg
                          width="1em"
                          height="1em"
                          viewBox="0 0 16 16"
                          className="bi bi-pencil"
                          fill="currentColor"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            fillRule="evenodd"
                            d="M11.293 1.293a1 1 0 0 1 1.414 0l2 2a1 1 0 0 1 0 1.414l-9 9a1 1 0 0 1-.39.242l-3 1a1 1 0 0 1-1.266-1.265l1-3a1 1 0 0 1 .242-.391l9-9zM12 2l2 2-9 9-3 1 1-3 9-9z"
                          />
                          <path
                            fillRule="evenodd"
                            d="M12.146 6.354l-2.5-2.5.708-.708 2.5 2.5-.707.708zM3 10v.5a.5.5 0 0 0 .5.5H4v.5a.5.5 0 0 0 .5.5H5v.5a.5.5 0 0 0 .5.5H6v-1.5a.5.5 0 0 0-.5-.5H5v-.5a.5.5 0 0 0-.5-.5H3z"
                          />
                        </svg>
                      </Button>
                    )}
                    {(t.novo || t.editando) && (
                      <Button
                        className="icon"
                        title="Confirmar"
                        variant="success"
                        onClick={() => handleSalvar(t.id)}
                      >
                        <svg
                          width="1em"
                          height="1em"
                          viewBox="0 0 16 16"
                          className="bi bi-check2"
                          fill="currentColor"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            fillRule="evenodd"
                            d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0z"
                          />
                        </svg>
                      </Button>
                    )}

                    {!t.novo && (
                      <Button
                        className="icon"
                        title="Excluir"
                        onClick={() =>
                          Modal.showModal(
                            'Excluir Telefone',
                            'Deseja realmente excluir este telefone?',

                            handleExcluir.bind(this, t.id)
                          )
                        }
                      >
                        <svg
                          width="1em"
                          height="1em"
                          viewBox="0 0 16 16"
                          className="bi bi-trash"
                          fill="currentColor"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                          <path
                            fillRule="evenodd"
                            d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
                          />
                        </svg>
                      </Button>
                    )}
                  </div>
                </Col>
              </Row>
              <Line />
            </Container>
          </Content>
        ))
      ) : (
        <h5>no h emails</h5>
      )}
      <BlocoBottom className="botao">
        <Row>
          <Col md={4} xs={12} className="bt-adicionar-telefone">
            <Button
              variant="success"
              onClick={() => handleAdd()}
              className="plus"
              title="Novo e-mail"
              disabled={funcionario.id === '' ? 'disabled' : ''}
            >
              <svg
                width="1em"
                height="1em"
                viewBox="0 0 16 16"
                className="bi bi-plus"
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z"
                />
              </svg>
            </Button>
          </Col>
        </Row>
      </BlocoBottom>
    </>
  );
}

ListaEmails.propTypes = {
  data: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
  tipo: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
  funcionario: PropTypes.oneOfType([PropTypes.object]),
};
ListaEmails.defaultProps = {
  data: [],
  tipo: [],
  funcionario: {},
};
