import React from 'react';
import PropTypes from 'prop-types';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { toast } from 'react-toastify';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Content } from './styles';

export default function Home(props) {
  const { handleAbrirPagina } = props;
  const notify = () => toast('Wow so easy !');
  return (
    <Content>
      <div className="tela">
        <div>
          <Row>
            <Col sm={8}>
              <Form.Group>
                <Form.Label>Descrio do cargo</Form.Label>
                <Form.Control type="text" className="descricao" />
              </Form.Group>
            </Col>
            <Col sm={4}>
              <Form.Group>
                <Form.Label>Status: </Form.Label>
                <Form.Check type="radio" label="Sim" name="ativo" id="1" />
                <Form.Check type="radio" label="No" name="ativo" id="2" />
              </Form.Group>
            </Col>
          </Row>
        </div>
        <Row>
          <Col sm={4}>
            <Button
              onClick={() => notify()}
              variant="success"
              className="btnSec"
            >
              acao
            </Button>
          </Col>
          <Col sm={4}>
            <Button
              className="btnPrincipal"
              onClick={() => handleAbrirPagina('/configuracoes')}
              variant="success"
            >
              Ir para configuraes
            </Button>
          </Col>
        </Row>
      </div>
    </Content>
  );
}

Home.propTypes = {
  handleAbrirPagina: PropTypes.oneOfType([PropTypes.element, PropTypes.func])
    .isRequired,
};
