import React from 'react';
import { Form } from 'react-bootstrap';
import PropTypes from 'prop-types';
// import { Container } from './styles';

export default function LabelError(props) {
  const { error, name } = props;

  return (
    <>
      {error && error[name] ? (
        <Form.Text className="text-error">{error[name].menssagem}</Form.Text>
      ) : (
        ''
      )}
    </>
  );
}

LabelError.propTypes = {
  name: PropTypes.string.isRequired,
  error: PropTypes.oneOfType([PropTypes.object]),
};
LabelError.defaultProps = {
  error: {},
};
