import React from 'react';
import PropTypes from 'prop-types';
import {
  Container,
  Row,
  Col,
  Form,
  Button,
  Card,
  Table,
} from 'react-bootstrap';
import { toast } from 'react-toastify';
import { FileIcon } from 'react-file-utils';
import * as Yup from 'yup';
import { GrDownload } from 'react-icons/gr';
import ServiceFuncionario from '../../services/serviceFuncionario';
import ServiceGlobal from '../../services/serviceGlobal';
import { Content, BlocoBottom } from './style';
import Modal from '../modal/index';
import InputText from '../form/inputText';

import IconExcluir from '../icons/excluir';
import IconEditar from '../icons/editar';
import IconNovo from '../icons/novo';
import IconSalvar from '../icons/salvar';
import IconsCancelar from '../icons/cancelar';

export default function ListaDocumentos(props) {
  const { data, tipo, funcionario } = props;
  const [listDocumentos, setListDocumentos] = React.useState(data);
  const [errosForm, setErrosForm] = React.useState();
  const [excluirArquivos, setExcluirArquivos] = React.useState([]);

  function changeInput(key, campo, e) {
    setListDocumentos(
      listDocumentos.map(item =>
        item.id === key ? { ...item, [campo]: e.target.value } : item
      )
    );
  }

  const handleEditar = id => {
    setListDocumentos(
      listDocumentos.map(item =>
        item.id === id ? { ...item, editando: true } : item
      )
    );
  };

  const handleCancelar = id => {
    const documentos = [];
    for (let x = 0; x < listDocumentos.length; x += 1) {
      if (listDocumentos[x].id === id) {
        if (!listDocumentos[x].novo) {
          documentos.push({ ...listDocumentos[x], editando: false });
        }
      } else {
        documentos.push({ ...listDocumentos[x], editando: false });
      }
    }

    setListDocumentos(documentos);
  };

  const handleAdd = () => {
    /* if (funcionario.id !== '') {
      let permitir = true;

      for (let x = 0; x < listDocumentos.length; x += 1) {
        if (listDocumentos[x].endereco === '') {
          permitir = false;
        }
      }

      if (permitir) {
        const enderecos = [];

        for (let x = 0; x < listDocumentos.length; x += 1) {
          enderecos.push({
            id: listEnderecos[x].id,
            cep: listEnderecos[x].cep,
            endereco: listEnderecos[x].endereco,
            complemento: listEnderecos[x].complemento,
            bairro: listEnderecos[x].bairro,
            estado: listEnderecos[x].estado,
            cidade: listEnderecos[x].cidade,
            numero: listEnderecos[x].numero,
          });
        }

        enderecos.push({
          id: Math.random()
            .toString(36)
            .substring(7),
          cep: '',
          endereco: '',
          complemento: '',
          bairro: '',
          estado: '',
          cidade: '',
          numero: '',
          novo: true,
        });

        setListEnderecos(enderecos);
      } else {
        toast.error('Ops! preencha os campos corretamente antes de adicionar');
      }
    } */
  };

  const handleSalvar = async id => {
    setErrosForm(null);
    let editando;
    const documento = {
      id: '',
      nome: '',
      descricao: '',
      tipo_documento_id: '',
      funcionario_id: funcionario.id,
      arquivos: [],
    };

    try {
      for (let x = 0; x < listDocumentos.length; x += 1) {
        if (listDocumentos[x].id === id) {
          documento.id = listDocumentos[x].id;
          documento.nome = listDocumentos[x].nome;
          documento.descricao = listDocumentos[x].descricao;
          documento.tipo_documento_id = listDocumentos[x].tipo_documento_id;
          editando = listDocumentos[x].editando;

          const arquivosDoc = [];
          for (let arq = 0; arq < listDocumentos[x].arquivos.length; arq += 1) {
            arquivosDoc.push(listDocumentos[x].arquivos[arq].id);
          }
          documento.arquivos = arquivosDoc;

          break;
        }
      }

      const schema = Yup.object().shape({
        tipo_documento_id: Yup.number().required('Informe o tipo'),
        nome: Yup.string().required('Informe o Nome'),
        descricao: Yup.string().required('Informe a descrio'),
        funcionario_id: Yup.number().required(),
      });

      await schema.validate(documento, {
        abortEarly: false,
      });

      let retorno;
      if (editando) {
        retorno = await ServiceFuncionario.editarDocumento(documento);
      } else {
        retorno = await ServiceFuncionario.salvarDocumento(documento);
      }

      if (retorno && retorno.data.status) {
        const dataRetorno = retorno.data.data;
        const documentos = [];

        for (let x = 0; x < listDocumentos.length; x += 1) {
          documentos.push({
            id:
              listDocumentos[x].id === id && dataRetorno.id
                ? dataRetorno.id
                : listDocumentos[x].id,
            nome: listDocumentos[x].nome,
            descricao: listDocumentos[x].descricao,
            tipo_documento_id: listDocumentos[x].tipo_documento_id,
            arquivos: listDocumentos[x].arquivos,
            novo: listDocumentos[x].id === id ? false : listDocumentos[x].novo,
            editando:
              listDocumentos[x].editando && listDocumentos[x].id === id
                ? false
                : listDocumentos[x].editando,
          });
        }

        if (editando) {
          toast.success('Documento Atualizado com sucesso');
        } else {
          toast.success('Documento salvo com sucesso');
        }
        setListDocumentos(documentos);
      } else {
        toast.error(retorno.data.data.motivo);
      }
    } catch (err) {
      if (err instanceof Yup.ValidationError) {
        const errorMessages = {};
        err.inner.forEach(error => {
          errorMessages[error.path] = { id, menssagem: error.message };
        });
        setErrosForm(errorMessages);
        toast.error('Preencha os campos corretamente');
      } else {
        toast.error('Ocorreu um erro inesperado');
      }
    }
  };

  const anexarArquivo = async (docId, formData) => {
    document.getElementById('fileDocument').value = null;
    const retorno = await ServiceGlobal.salvarArquivo(formData);
    const docs = [];
    for (let x = 0; x < listDocumentos.length; x += 1) {
      if (listDocumentos[x].id === docId) {
        const docAux = listDocumentos[x];
        const files = [];
        for (let arq = 0; arq < docAux.arquivos.length; arq += 1) {
          files.push(docAux.arquivos[arq]);
        }
        const newFile = {
          filename: retorno.data.data.filename,
          id: retorno.data.data.id,
          mimetype: retorno.data.data.mimetype,
          nome: retorno.data.data.nome,
        };
        files.push(newFile);
        docAux.arquivos = files;
        docs.push(docAux);
      } else {
        docs.push(listDocumentos[x]);
      }
    }
    setListDocumentos(docs);
  };

  const handleExcluirDocumento = async id => {
    const retorno = await ServiceFuncionario.excluirDocumento(id);
    if (retorno && retorno.status) {
      const listaAux = [];

      for (let x = 0; x < listDocumentos.length; x += 1) {
        if (listDocumentos[x].id !== id) {
          listaAux.push(listDocumentos[x]);
        }
      }
      setListDocumentos(listaAux);
      toast.success('Documento excluido com sucesso');
    }
  };

  const handleExcluirArquivo = async (id, listaArquivos, idDocumento) => {
    const listaExcluidosAux = [];
    for (let x = 0; x < excluirArquivos.length; x += 1) {
      if (excluirArquivos[x] !== id) {
        listaExcluidosAux.push(excluirArquivos[x]);
      }
    }
    listaExcluidosAux.push(id);
    setExcluirArquivos(listaExcluidosAux);

    const listaAux = [];
    for (let x = 0; x < listaArquivos.length; x += 1) {
      if (listaArquivos[x].id !== id) {
        listaAux.push(listaArquivos[x]);
      }
    }

    for (let x = 0; x < listDocumentos.length; x += 1) {
      if (listDocumentos[x].id === idDocumento) {
        listDocumentos[x].arquivos = listaAux;
      }
    }
  };

  return (
    <>
      {listDocumentos.length > 0 ? (
        listDocumentos.map(e =>
          e.editando || e.novo ? (
            <Content key={e.id}>
              <Container fluid>
                <Card>
                  <Card.Body>
                    <Row>
                      <Col md={6} xs={12}>
                        <InputText
                          id={e.id}
                          label="Nome"
                          name="nome"
                          value={e.nome}
                          changeInput={changeInput}
                          errosForm={errosForm}
                        />
                      </Col>
                      <Col md={6} xs={12}>
                        <InputText
                          id={e.id}
                          label="descrio"
                          name="descricao"
                          value={e.descricao}
                          changeInput={changeInput}
                          errosForm={errosForm}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col md={4} xs={12}>
                        <Form.Label>Tipo</Form.Label>
                        <Form.Control
                          as="select"
                          name="tipo_documento_id"
                          custom
                          value={e.tipo_documento_id}
                          onChange={chang =>
                            changeInput(e.id, 'tipo_documento_id', chang)
                          }
                          className={`form-control ${
                            errosForm &&
                            errosForm.tipo_documento_id &&
                            errosForm.tipo_documento_id.id === e.id
                              ? 'input-error'
                              : ''
                          }`}
                        >
                          <option value="selecione" key="selecione">
                            selecione
                          </option>
                          {tipo.map(tip => (
                            <option value={tip.id} key={tip.id}>
                              {tip.nome}
                            </option>
                          ))}
                        </Form.Control>
                      </Col>
                    </Row>
                    <Row>
                      <p>Arquivos do Documento</p>
                      <Table striped bordered hover>
                        <thead>
                          <tr>
                            <th>id</th>
                            <th>NOME</th>
                            <th>TIPO</th>
                            <th />
                          </tr>
                        </thead>
                        <tbody>
                          {e.arquivos.map(a => (
                            <tr>
                              <td>{a.id}</td>
                              <td>{a.nome}</td>
                              <td>
                                <FileIcon
                                  mimeType={a.mimetype}
                                  filename={a.filename}
                                  big
                                />
                              </td>
                              <td>
                                <Row>
                                  <Col md={12} xs={12} className="botao">
                                    <Button
                                      className="icon"
                                      title="Exluir"
                                      onClick={() =>
                                        Modal.showModal(
                                          'Excluir Arquivo',
                                          'Deseja realmente excluir este arquivo?',
                                          handleExcluirArquivo.bind(
                                            this,
                                            a.id,
                                            e.arquivos,
                                            e.id
                                          )
                                        )
                                      }
                                    >
                                      <IconExcluir />
                                    </Button>
                                  </Col>
                                </Row>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </Table>
                    </Row>
                    <BlocoBottom className="botao">
                      <Row>
                        <Col md={4} xs={12} className="bt-adicionar-documento">
                          <div className="uploadButtom">
                            <input
                              type="file"
                              id="fileDocument"
                              onChange={eFile => {
                                Modal.fileName(e.id, eFile, anexarArquivo);
                              }}
                              onClick={inp => (inp.target.value = null)}
                            />
                            <IconNovo />
                          </div>
                        </Col>
                      </Row>
                    </BlocoBottom>

                    <Row className="botao">
                      <Col md={2} xs={12} className="botao">
                        <Button
                          className="icon"
                          title="Salvar"
                          onClick={() => handleSalvar(e.id)}
                        >
                          <IconSalvar />
                        </Button>
                        <Button
                          className="icon"
                          title="Cancelar"
                          onClick={() => handleCancelar(e.id)}
                        >
                          <IconsCancelar />
                        </Button>
                      </Col>
                    </Row>
                  </Card.Body>
                </Card>
              </Container>
            </Content>
          ) : (
            <Content key={e.id}>
              <Container fluid>
                <Card>
                  <Card.Body>
                    <Row>
                      <Col md={12} xs={12}>
                        <Form.Label>
                          <strong>Nome:</strong>
                          {e.nome}
                        </Form.Label>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={12} xs={12}>
                        <Form.Label>
                          <strong>Descrio:</strong>
                          {e.descricao}
                        </Form.Label>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={12} xs={12}>
                        <Table striped bordered hover>
                          <thead>
                            <tr>
                              <th>NOME</th>
                              <th>TIPO</th>
                              <th />
                            </tr>
                          </thead>
                          <tbody>
                            {e.arquivos.map(a => (
                              <tr>
                                <td>{a.nome}</td>
                                <td>
                                  <FileIcon
                                    mimeType={a.mimetype}
                                    filename={a.filename}
                                    big
                                  />
                                </td>
                                <td>
                                  <a
                                    href={`http://localhost:3333/files/${a.filename}`}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    download={`http://localhost:3333/files/${a.filename}`}
                                  >
                                    <Button
                                      variant="outline-info"
                                      size="sm"
                                      block
                                    >
                                      <GrDownload className="icone-file" />
                                    </Button>
                                  </a>
                                </td>
                              </tr>
                            ))}
                          </tbody>
                        </Table>
                      </Col>
                    </Row>

                    <Row className="botao">
                      <Col md={2} xs={12} className="botao">
                        <Button
                          title="Editar"
                          className="icon"
                          onClick={() => handleEditar(e.id)}
                        >
                          <IconEditar />
                        </Button>
                        <Button
                          className="icon"
                          title="Excluir"
                          onClick={() =>
                            Modal.showModal(
                              'Excluir Endereo',
                              'Deseja realmente excluir este documento?',
                              handleExcluirDocumento.bind(this, e.id)
                            )
                          }
                        >
                          <IconExcluir />
                        </Button>
                      </Col>
                    </Row>
                    <Row />
                  </Card.Body>
                </Card>
              </Container>
            </Content>
          )
        )
      ) : (
        <h5>no h Documentos</h5>
      )}
      <BlocoBottom className="botao">
        <Row>
          <Col md={4} xs={12} className="bt-adicionar-documento">
            <Button
              variant="success"
              className="plus"
              disabled={funcionario.id === '' ? 'disabled' : ''}
              onClick={() => handleAdd()}
              title="Novo Documento"
            >
              <IconNovo />
            </Button>
          </Col>
        </Row>
      </BlocoBottom>
    </>
  );
}

ListaDocumentos.propTypes = {
  data: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
  funcionario: PropTypes.oneOfType([PropTypes.object]),
};
ListaDocumentos.defaultProps = {
  data: [],
  funcionario: {},
};
