import React from 'react';
import PropTypes from 'prop-types';
import { Nav, NavDropdown, Button } from 'react-bootstrap';
import { GiHamburgerMenu } from 'react-icons/gi';
import { useDispatch, useSelector } from 'react-redux';
import { logout } from '../../store/modules/auth/actions';
import { Content } from './style';

export default function Header(props) {
  const { handleDrawer, sidebarDocked } = props;
  const dispatch = useDispatch();
  const { usuario } = useSelector(state => state.usuario);

  function handleLogout() {
    dispatch(logout());
  }

  return (
    <Content>
      <Nav
        activeKey="/home"
        onSelect={selectedKey => alert(`selected ${selectedKey}`)}
        className="justify-content-end navBar"
      >
        {!sidebarDocked && (
          <Button variant="link" onClick={() => handleDrawer(true)}>
            <GiHamburgerMenu />
          </Button>
        )}

        <div className="usuario">
          <NavDropdown
            title={usuario ? usuario.nome : 'Usurio'}
            id="basic-nav-dropdown"
            className="navBarMenu"
          >
            <NavDropdown.Item className="navBarItem" href="#">
              Minha conta
            </NavDropdown.Item>

            <NavDropdown.Divider />
            <NavDropdown.Item href="#" className="navBarItem">
              Configuraes
            </NavDropdown.Item>
            <NavDropdown.Item
              onClick={() => {
                handleLogout();
              }}
              className="navBarItem"
            >
              Sair
            </NavDropdown.Item>
          </NavDropdown>
        </div>
      </Nav>
    </Content>
  );
}

Header.propTypes = {
  handleDrawer: PropTypes.oneOfType([PropTypes.element, PropTypes.func])
    .isRequired,
  sidebarDocked: PropTypes.bool.isRequired,
};
