import styled from 'styled-components';

export const Content = styled.div`
  width: 100%;
  height: 100%;
  background: #f8f7fb;
  padding: 15px;
  button {
    color: #745;
  }
  div {
    display: inline;
  }
  .tela {
    padding: 50px;
    width: 90%;
    height: 80%;
    background: #fff;
    border: #000;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.14), 0 3px 4px 0 rgba(0, 0, 0, 0.12),
      0 1px 5px 0 rgba(0, 0, 0, 0.2);
    margin-left: 65px;
    margin-top: 80px;
    position: absolute;
    display: block;
  }
  input {
    border: transparent;
    border-bottom: 1px solid #bbbbbb;
    border-radius: 0px;
    margin-bottom: 40px;

    &:focus {
      box-shadow: none;
      border-bottom-width: 1px solid #698ca4;
    }
  }

  label {
    border-top: 10px;
    border-bottom: 0px;
  }

  .btnPrincipal {
    color: #fff;
    background-color: #39557c;
    border-color: #39557c;
    margin: 30px;
    font-size: 20px;

    &:hover {
      color: #fff;
      background-color: #698ca4;
      border-color: #698ca4;
    }
  }

  button.btnPrincipal.btn.btn-success {
    &:active {
      box-shadow: none;
      color: #fff;
      background-color: #698ca4;
      border-color: #698ca4;
    }
  }

  .btnSec {
    color: #848484;
    background-color: transparent;
    border-color: #848484;
    &:hover {
      color: #fff;
      background-color: #848484;
      border-color: #848484;
    }
  }
`;
