import produce from 'immer';

const INITIAL_STATE = {
  usuario: null,
};

export default function usuario(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case '@auth/LOGIN_SUCCESS': {
        draft.usuario = action.payload.usuario;
        break;
      }
      case '@auth/LOGOUT_SUCCESS': {
        draft.usuario = null;
        break;
      }
      default:
    }
  });
}
