import React from 'react';
import { Form } from 'react-bootstrap';
import LabelErro from '../labelError/index';
import HasError from '../classErrorInput';

function InputOptions(props) {
  const { id, label, name, errosForm, value, changeInput, Options } = props;
  return (
    <Form.Group>
      <Form.Label>{label}</Form.Label>

      {name && changeInput && (
        <>
          <Form.Control
            as="select"
            name={name}
            custom
            value={value}
            className={`form-control ${HasError(errosForm, name)}`}
            onChange={e =>
              id ? changeInput(id, name, e) : changeInput(name, e)
            }
          >
            <option value="" key="selecione">
              -
            </option>
            <Options />
          </Form.Control>
          {errosForm && <LabelErro error={errosForm} name={name} />}
        </>
      )}
    </Form.Group>
  );
}

export default InputOptions;
