import styled from 'styled-components';
import fundo from '../../assets/fundoLogin.jpg';

export const Content = styled.div`
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background: url(${fundo}) no-repeat center top fixed;
  background-size: 100% 100%;

  .login {
    width: 400px;
    height: 450px;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.14), 0 3px 4px 0 rgba(0, 0, 0, 0.12),
      0 1px 5px 0 rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    background-color: #ffffffcc;
    input{
      background: transparent;
    }

    @media (max-width: 400px) {
      box-shadow:none;
      background-color: transparent;
    }
  }

  .top-login {
    background: #334150;
    color: #fff;
    height: 50px;
    padding-top: 7px;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.14), 0 3px 4px 0 rgba(0, 0, 0, 0.12),
      0 1px 5px 0 rgba(0, 0, 0, 0.2);

    border-radius: 5px 5px 0px 0px;
    @media (max-width: 400px) {
      box-shadow:none;
      background:none;
      color: #334150;
    }

  }

  h3 {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  form {
    padding: 30px;
  }

  button.loginPrincipal {
    color: #fff;
    background-color: #334150;
    border-color: #334150;
    width: 100%;
    display: block;
    margin: 30px auto;
    font-size: 20px;

    &:hover {
      color: #fff;
      background-color: #39557c;
      border-color: #39557c;
    }
  }

  button.loginPrincipal.btn.btn-primary{
    &:active{
      box-shadow: none;
      color: #fff;
      background-color: #698ca4;
      border-color: #698ca4;
    }
  }

  button.loginSec {
    color: #848484;
    background-color: transparent;
    border-color: transparent;
    width: 100%;
    display: block;
    margin: 30px auto;
    &:hover {
      color: #848484;
      background-color: transparent;
      border-color: transparent;
      text-decoration: underline;
    }

  }

  button.loginSec.btn.btn-primary{
    &:active{
      color: #848484;
      background-color: transparent;
      border-color: transparent;
      text-decoration: underline;
      box-shadow: none;
    }
  }

  input {
    border: transparent;
    border-bottom: 1px solid #BBBBBB;
    border-radius: 0px;
    margin-bottom: 40px;

    &:focus {
      box-shadow: none;
      border-bottom-width: 1px solid #698ca4;
    }
  }

  label{
    border-top: 10px;
    border-bottom: 0px;
    }
  }

  .faixa {
    position: fixed;
    width: 100%;
    height: 100%;
    border-top: 20px solid #334150;
    z-index: -1;
  }

  .btn-primary{
    border: none !important;
    color: none !important;
  }

`;
